<template>
	<div>
		{{ selectedTab }}
		<nav>
			<ul>
				<li><a href="#tab-poses">Poses</a></li>
				<li><a href="#tab-sequences">Sequences</a></li>
			</ul>
		</nav>
		<poses v-if="selectedTab === 'poses' || selectedTab === ''"></poses>
		<sequences v-if="selectedTab === 'sequences'"></sequences>
		<pose v-if="selectedPose.hasOwnProperty('id')" v-bind:pose="selectedPose"></pose>
	</div>
</template>

<script>
	import Store from './model/Store'
	import Poses from './components/Poses'
	import Sequences from './components/Sequences'
	import Pose from './components/Pose'
	export default {
		name: 'app',
		data () {
			return {
				selectedPose: {},
				selectedTab: 'poses'
			}
		},
		mounted () {
			window.onhashchange = this.hashChanged
		},
		components: {
			'poses': Poses,
			'sequences': Sequences,
			'pose': Pose
		},
		methods: {
			hashChanged (evt) {
				let hash = window.location.hash.split('-')
				let linkType = hash[0]
				if (linkType === '#tab') {
					this.$data.selectedTab = hash[1]
				} else if (linkType === '#pose') {
					this.$data.selectedPose = Store.getPose(window.location.hash)
				}
			}
		}
	}
</script>

<style lang="scss">
</style>



