<template>
	<ul>
		<li v-for="pose in poses" v-if="filterPoses(pose, selectedType, selectedExperience)">
			<a v-bind:href="pose.url">
				<span>{{ pose.name.english }} ({{ pose.name.sanskrit }})</span>
				<span>{{ pose.type }}</span>
				<span>{{ pose.experience }}</span>
				<img class="icon" v-bind:src="pose.media.icon" v-bind:alt="pose.name.english">
			</a>
		</li>
	</ul>
</template>

<script>
	export default {
		name: 'poses-grid',
		data () {
			return {
			}
		},
		props: [ 'poses', 'selected-type', 'selected-experience' ],
		methods: {
			filterPoses (pose, selectedType, selectedExperience) {
				return (pose.type === selectedType || selectedType === 'all') && (pose.experience === selectedExperience || selectedExperience === 'all')
			}
		}
	}
</script>

<style lang="scss" scoped>
	.icon {
		width: 100px;
	}
</style>



