import axios from 'axios'

let state = {
	poses: [],
	types: [],
	experiences: [],
	sequences: []
}

function getData() {
	axios.get('/dist/test.json').then(function(response) {
		setPoses(response.data.poses)
		setSequences(response.data.sequences)
	}).then(function() {
		console.log(state)
	})
}

function setPoses(json) {
	let types = []
	let experiences = []
	for (let i = 0; i < json.length; i++) {
		let item = json[i]
		if (!types.hasOwnProperty(item.type)) {
			types[item.type] = item.type
			state.types.push(item.type)
		}
		if (!experiences.hasOwnProperty(item.experience)) {
			experiences[item.experience] = item.experience
			state.experiences.push(item.experience)
		}
		item.url = '#' + item.id
		state.poses.push(item)
	}
}
function setSequences(json) {
	for (let i = 0; i < json.length; i++) {
		let item = json[i]
		state.sequences.push(item)
	}
}

function getPose(hash) {
	let returnedPose = {}
	for (let i = 0; i < state.poses.length; i++) {
		let pose = state.poses[i]
		if (pose.url === hash) {
			returnedPose = pose
			break
		}
	}
	return returnedPose
}

export default {
	getData: getData,
	getPose: getPose,
	poses: state.poses,
	sequences: state.sequences,
	types: state.types,
	experiences: state.experiences
}



