import Vue from 'vue'
import App from './App'
import Store from './model/Store'

Store.getData()

// Typical instantiation
new Vue({
	el: '#app',
	// Two ways to mount:
	// 1) include <App/> or <app/> in index.html
	// 2) include this in new Vue's object:
	// 			template: '<App/>',
	// - Argument for #1 is
	// 		a) if you want content that is not overridden by Vue
	// 		b) allows for non-javascript fallback
	// 		c) strict guidelines are fairly unspecific in the Vue docs as Vue is "unopinionated"
	components: {
		App
	}
})

// CLI's instantiation
// Explanation: https://github.com/vuejs-templates/webpack-simple/issues/29
//  - shorthand for JSX render - doesn't require <App/> or <app/> anywhere in index.html
// new Vue({
// 	el: '#app',
// 	render: h => h(App)
// })



