<template>
	<ul>
		<li>Filter by Type:
			<select v-on:change="typeChanged">
				<option value="all">All</option>
				<option v-for="type in types" v-bind:value="type">
					{{ type }}
				</option>
			</select>
		</li>
		<li>Filter by Experience:
			<select v-on:change="experienceChanged">
				<option value="all">All</option>
				<option v-for="experience in experiences" v-bind:value="experience">
					{{ experience }}
				</option>
			</select>
		</li>
	</ul>
</template>

<script>
	export default {
		name: 'poses-filter',
		data () {
			return {
			}
		},
		props: [ 'types', 'experiences' ],
		methods: {
			typeChanged: function(evt) {
				this.$emit('type-changed', evt)
			},
			experienceChanged: function(evt) {
				this.$emit('experience-changed', evt)
			}
		}
	}
</script>

<style lang="scss">
</style>



