<template>
	<div>
		<span>{{ pose.id }}</span>
		<span>{{ pose.name.english }} ({{ pose.name.sanskrit }})</span>
		<span>{{ pose.type }}</span>
		<span>{{ pose.experience }}</span>
	</div>
</template>

<script>
	export default {
		name: 'pose',
		data () {
			return {
			}
		},
		props: [ 'pose' ]
	}
</script>

<style lang="scss">
</style>



