<template>
	<ul>
		<li v-for="sequence in sequences">{{ sequence }}</li>
	</ul>
</template>

<script>
	import Store from '../model/Store'
	export default {
		name: 'sequences',
		data () {
			return {
				poses: Store.poses,
				sequences: Store.sequences
			}
		},
		methods: {
		}
	}
</script>

<style lang="scss">
